<template>
  <el-dialog
    class="custom-dialog"
    width="600"
    :title="getTitle"
    :model-value="visible"
    @close="handleClose"
  >
    <div v-if="props.isBind">
      <el-form
        class="form"
        ref="formRef"
        :model="formData"
        :rules="rules"
        label-width="100px"
      >
        <el-form-item label="手机号码" prop="staffNumber">
          <el-input
            v-model="formData.staffNumber"
            placeholder="请输入手机号码"
          ></el-input>
        </el-form-item>
        <el-form-item label="图形验证码" prop="imageCode" class="verification">
          <el-input
            class="input-width"
            v-model="formData.imageCode"
            placeholder="请输入图形验证码"
          ></el-input>
          <div class="picture-code">
            <img :src="paramsData.codeImageUrl" @click="getCodeImage" alt="" />
          </div>
        </el-form-item>
        <el-form-item
          label="短信验证码"
          prop="validateCode"
          class="verification"
        >
          <el-input
            class="input-width"
            v-model="formData.validateCode"
            placeholder="请输入短信验证码"
          ></el-input>
          <div class="telephone-code">
            <span class="count" v-if="!paramsData.telephoneCodeObj.flag"
              >{{ paramsData.telephoneCodeObj.count }}s</span
            >
            <span v-else @click="getTelephoneCode">获取验证码</span>
          </div>
        </el-form-item>
        <h4>
          绑定成功后，请留意手机号码短信，运营商系统将向你发送短信实名信息采集链接，请在<br />
          一周内完成实名信息
        </h4>
      </el-form>
    </div>
    <div v-else class="unBind">确认解绑主叫号码和虚拟号码间的绑定关系！</div>
    <template #footer>
      <div class="dialog-footer">
        <el-button type="primary" @click="submit">确定</el-button>
        <el-button @click="handleClose">取消</el-button>
      </div>
    </template>
  </el-dialog>
</template>
<script lang="ts" setup>
import { ref, reactive, shallowRef, computed } from "vue";
import { ElMessage } from "element-plus";
import { testTelephone } from "jz-tool-lib";
import { checkoutTelephone } from "@assets/validatorHook";
import { codeImageApi2 } from "@api/common-api";
import { bindApi, unbindApi, sendSmsApi } from "@api/phone-num-order";

const visible = shallowRef(false);
const emits = defineEmits<{
  (e: "emitFunc"): void;
  (e: "cancel", value: any): void;
}>();
const props = defineProps<{
  isBind: boolean;
}>();
const formRef = ref();
const formData = reactive<any>({
  companyId: null, // 公司id
  phoneNumManageId: "", // 工作号号码ID
  staffNumber: "", // 员工号（绑定手机号）
  validateCode: "", //短信验证码
  xnumber: "",
  imageCode: "",
});
const paramsData = reactive<any>({
  telephoneCodeObj: {
    flag: true,
    timer: 0,
    count: 60,
  },
  telephoneCodeFlag: false,
  codeImageUrl: "",
  uuid: "",
});
const rules = {
  staffNumber: [
    {
      required: true,
      validator: (rule: any, value: string, callback: any) => {
        checkoutTelephone(rule, value, callback);
      },
      trigger: "blur",
    },
  ],
  imageCode: [
    {
      required: true,
      message: "请输入图形验证码",
      trigger: "blur",
    },
  ],
  validateCode: [
    {
      required: true,
      message: "请输入短信验证码",
      trigger: "blur",
    },
  ],
};
const getTitle = computed(() => {
  return `${props.isBind ? "绑定" : "解绑"}手机号`;
});
const handleOpen = async () => {
  visible.value = true;
  await getCodeImage();
};
const handleClose = () => {
  emits("cancel", { callback: () => {} });
  visible.value = false;
  clearInterval(paramsData.telephoneCodeObj.timer);
  paramsData.telephoneCodeObj.flag = true;
  paramsData.telephoneCodeObj.count = 60;
  if (props.isBind) {
    formRef.value.resetFields();
  }
};
/*
 * 获取图形验证码
 * */
const getCodeImage = async () => {
  const res = await codeImageApi2();
  const { img, uuid } = res.data;
  paramsData.codeImageUrl = `data:image/png;base64,${img}`;
  paramsData.uuid = uuid;
};

// 获取短信验证码
const getTelephoneCode = () => {
  const { staffNumber } = formData;
  const { telephoneCodeFlag } = paramsData;
  if (telephoneCodeFlag) return;
  paramsData.telephoneCodeFlag = true;
  if (!staffNumber) {
    ElMessage.error("请输入手机号");
    return resetFlag();
  }
  if (!testTelephone(staffNumber)) {
    ElMessage.error("请输入正确的手机号码");
    return resetFlag();
  }
  if (!formData.imageCode) {
    ElMessage.error("请输入图形验证码");
    return resetFlag();
  }
  const params = {
    phone: staffNumber,
    imageCode: formData.imageCode,
    uuid: paramsData.uuid,
  };
  sendSmsApi
    .getData(params)
    .then(() => {
      ElMessage.success("短信已发送");
      telephoneCodeInterval();
    })
    .catch(async () => {
      await getCodeImage();
    })
    .finally(resetFlag);
};
const resetFlag = () => {
  paramsData.telephoneCodeFlag = false;
};

// 短信验证码倒计时
const telephoneCodeInterval = () => {
  paramsData.telephoneCodeObj.timer = setInterval(() => {
    if (paramsData.telephoneCodeObj.count === 0) {
      clearInterval(paramsData.telephoneCodeObj.timer);
      paramsData.telephoneCodeObj.flag = true;
      paramsData.telephoneCodeObj.count = 60;
    } else {
      paramsData.telephoneCodeObj.flag = false;
      paramsData.telephoneCodeObj.count -= 1;
    }
  }, 1000);
};
const submit = () => {
  const commonHandler = () => {
    emits("emitFunc");
    visible.value = false;
  };
  if (props.isBind) {
    formRef.value.validate(async (valid: any) => {
      if (valid) {
        await handleBind();
        commonHandler();
        formRef.value.resetFields();
      }
    });
  } else {
    handleUnbind();
    commonHandler();
  }
  clearInterval(paramsData.telephoneCodeObj.timer);
  paramsData.telephoneCodeObj.flag = true;
  paramsData.telephoneCodeObj.count = 60;
};
//绑定
async function handleBind() {
  const newFormData = {
    companyId: formData.companyId,
    phoneNumManageId: formData.phoneNumManageId,
    staffNumber: formData.staffNumber,
    validateCode: formData.validateCode,
    xnumber: formData.xnumber,
  };
  const res = await bindApi.getData(newFormData as any);
  if (res.statusCode == 0) {
    ElMessage.success("绑定成功");
  }
}
//解绑
async function handleUnbind() {
  const newFormData = {
    companyId: formData.companyId,
    phoneNumManageId: formData.phoneNumManageId,
    // staffNumber: formData.staffNumber,
    // validateCode: formData.validateCode,
    // xnumber: formData.xnumber,
  };
  const res = await unbindApi.getData(newFormData as any);
  if (res.statusCode == 0) {
    ElMessage.success("解绑成功");
  }
}
//row
const setData = (data: any) => {
  const { companyId, id, phone } = data;
  formData.companyId = companyId;
  formData.phoneNumManageId = id;
  formData.xnumber = phone;
};
defineExpose({
  handleOpen,
  setData,
});
</script>

<style lang="scss" scoped>
.unBind {
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
}
h4 {
  color: #aaaaaa;
  font-size: 14px;
  font-weight: 400;
}
.input-width {
  width: 70%;
}
.verification {
  position: relative;

  :deep(.el-input__wrapper) {
    // padding-right: 95px;
  }
  .picture-code,
  .telephone-code {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    width: 90px;
    height: 40px;
    font-size: 14px;
    color: #0056ff;
    cursor: pointer;

    span {
      height: 100%;
    }
    img {
      width: 90%;
    }
  }
}

:deep(.el-input__inner:focus) {
  border-color: #0056ff;
}
</style>
