<template>
  <el-dialog
    class="custom-dialog"
    width="450px"
    title="申请换号"
    :model-value="visible"
    @close="handleClose"
    @open="handleOpen"
  >
    <div>
      <div class="tip">注：请谨慎操作！</div>
      <el-form
        class="form"
        ref="formRef"
        :model="formData"
        :rules="rules"
        label-width="100px"
      >
        <el-form-item label="换号原因" prop="reason">
          <el-select
            placeholder="请选择"
            v-model="formData.reason"
            class="verification"
          >
            <el-option
              v-for="item in reasonTypeHandler"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
<!--        <el-form-item label="图像验证码" prop="imageCode" class="verification">-->
<!--          <el-input-->
<!--            v-model="formData.imageCode"-->
<!--            placeholder="请输入图像验证码"-->
<!--          ></el-input>-->
<!--          <div class="picture-code">-->
<!--            <img-->
<!--              :src="paramsData.codeImageUrl"-->
<!--              @click.self="getCodeImage"-->
<!--              alt=""-->
<!--            />-->
<!--          </div>-->
<!--        </el-form-item>-->
      </el-form>
    </div>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取消</el-button>
        <el-button type="primary" @click="submit">确定</el-button>
      </div>
    </template>
  </el-dialog>
</template>
<script lang="ts" setup>
import { ref, reactive } from "vue";
import { codeImageApi2 } from "@api/common-api";
import { changeApi } from "@api/phone-num-order";
import { ElMessage } from "element-plus";
import { LocalStorage } from "jz-tool-lib";

const userInfo = LocalStorage.getItem("bd_userInfo");
const virCompanyId = LocalStorage.getItem("bd_virCompanyId");
const emits = defineEmits<{
  (e: "emitFunc"): void;
}>();
const formRef = ref();
const visible = ref(false);
let row = reactive({});
const formData = reactive({
  reason: "1",
  imageCode: "",
});
const paramsData = reactive({
  codeImageUrl: "",
  uuid: "",
});
const reasonType = [
  {
    label: "标记过多",
    value: "1",
  },
  {
    label: "被投诉",
    value: "2",
  },
  {
    label: "其他",
    value: "3",
  },
  {
    label: "不使用混显号码",
    value: "5",
  },
];

const reasonTypeHandler = ref([]);

const rules = {
  reason: [
    {
      required: true,
      message: "请选择换号原因",
      trigger: "blur",
    },
  ],
  // imageCode: [
  //   {
  //     required: true,
  //     message: "请输入图形验证码",
  //     trigger: "blur",
  //   },
  // ],
};

const handleOpen = () => {
  // getCodeImage();
};
const handleClose = () => {
  formRef.value.resetFields();
  visible.value = false;
};
/*
 * 获取图形验证码
 * */
const getCodeImage = () => {
  codeImageApi2({
    profileId: userInfo.profileId,
    companyId: row.companyId,
  }).then((res) => {
    paramsData.codeImageUrl = `data:image/png;base64,${res.data.img}`;
    paramsData.uuid = res.data.uuid;
  });
};

const submit = () => {
  formRef.value.validate(async (valid: boolean) => {
    if (valid) {
      const params = {
        changeReasonType: formData.reason,
        // checkCode: formData.imageCode,
        phoneId: row.id,
        operatorId: virCompanyId,
        phoneNumber: row.num,
        companyCode: row.companyCode,
        companyId: row.companyId,
        // uuid: paramsData.uuid,
        agentUserType: row.type, // 代理商用户类型 1:接口客户 0：直客
        numCompanyId: row.numCompanyId,
      };
      if (formData.reason === "5") {
        params.labelNames = row.sceneName;
      }
      const res = await changeApi.getData(params);
      ElMessage({
        message: res.msg || "换号中，请稍等",
        type: "success",
      });
      handleClose();
      setTimeout(() => {
        emits("emitFunc");
      }, 1000);
    } else {
      return false;
    }
  });
};

const toggle = (value, rowItem = {}) => {
  row = rowItem;
  if (!row.isMixed) {
    reasonTypeHandler.value = reasonType.filter((i) => i.value !== "5");
  } else {
    reasonTypeHandler.value = reasonType;
  }
  visible.value = value;
};

defineExpose({
  toggle,
});
</script>

<style lang="scss" scoped>
.verification {
  position: relative;

  :deep(.el-input__wrapper) {
    padding-right: 95px;
  }
  .picture-code,
  .telephone-code {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    width: 90px;
    height: 40px;
    font-size: 14px;
    color: #0056ff;
    cursor: pointer;

    span {
      height: 100%;
    }
    img {
      width: 90%;
      height: 90%;
    }
  }
}

:deep(.el-input__inner:focus) {
  border-color: #0056ff;
}
.tip {
  height: 50px;
  background: #f2f2f2;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 50px;
  border-radius: 4px;
  padding-left: 20px;
}
</style>
