<template>
  <div class="call-statistics">
    <div class="form-container">
      <TabList
        :list="tabList"
        :activeIndex="activeIndex"
        @emitTab="tabClick"
      ></TabList>
      <div class="search-box">
        <el-form :model="paramsData" class="form">
          <el-form-item label="号码：">
            <el-input
              v-model="paramsData.phone"
              placeholder="请输入"
              clearable
            />
          </el-form-item>
          <el-form-item label="号码状态：">
            <el-select
              placeholder="请选择"
              v-model="paramsData.phoneStatus"
              @change="search"
              clearable
              style="width: 200px"
            >
              <el-option
                v-for="item in shareStatusTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="margin-left: 20px" @click="search"
              >搜索</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="table-container">
      <!--   列表   -->
      <el-table :data="pageData.list" height="calc(100vh - 260px)">
        <el-table-column align="center" prop="phone" key="phone" label="号码">
          <template #default="{ row }">
            {{ row.phone }}
            <el-tag v-if="row.isMixed" size="small">混显</el-tag>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="city" key="city" label="归属地">
          <template #default="{ row }">
            <el-tooltip
              v-if="row.cityHandler"
              class="box-item"
              :content="row.city"
              placement="top"
            >
              {{ row.cityHandler }}
            </el-tooltip>
            <span v-else>{{ row.city }}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="useStatusName"
          key="useStatusName"
          label="号码状态"
        />
        <el-table-column
          v-if="activeIndex == '7'"
          align="center"
          prop="bindStatus"
          key="bindStatus"
          label="绑定状态"
        >
          <template #default="{ row }">
            {{ bindStatusMap[row.bindStatus] }}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="distributeNum"
          key="distributeNum"
          label="并发量"
        />
        <el-table-column
          align="center"
          prop="createTime"
          key="createTime"
          label="下号时间"
        />
        <el-table-column
          align="center"
          prop="endTime"
          key="endTime"
          label="到期时间"
        />
        <el-table-column align="center" label="操作" v-if="showOperate">
          <template #default="{ row }">
            <el-button
              type="text"
              @click="changeNum(row)"
              v-if="activeIndex !== '4' && activeIndex !== '7'"
              >换号</el-button
            >
            <el-button
              type="text"
              @click="handleAssign(row)"
              v-if="activeIndex == '7'"
              >{{
                row.bindStatus == 0
                  ? "绑定"
                  : row.bindStatus == 2
                  ? "解绑"
                  : row.bindStatus == 3
                  ? "绑定"
                  : ""
              }}</el-button
            >
          </template>
        </el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无数据"></TableEmpty>
        </template>
      </el-table>
      <!--   分页   -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
    <ChangeNumber ref="changeNumberRef" @emitFunc="getList" />
    <!-- 解绑/绑定 -->
    <HandleNumber
      ref="handleNumberRef"
      @emitFunc="getList"
      @cancel="handleCancel"
      :isBind="initData.isBind"
    />
  </div>
</template>
<script lang="ts" setup>
import { reactive, ref, shallowReactive, computed } from "vue";
import { usePage } from "jz-tool-lib";
import { phoneListApi } from "@api/phone-num-order";
import { useVueRouter } from "@assets/useHook";
import ChangeNumber from "@views/business-management/osprey-cloud-call/number-ordering/components/change-number.vue";
import HandleNumber from "@views/business-management/osprey-cloud-call/number-ordering/components/handle-number.vue";
const tabList = [
  {
    label: "虚拟号",
    value: "2",
  },
  {
    label: "固话",
    value: "4",
  },
  {
    label: "实体卡",
    value: "5",
  },
  {
    label: "虚拟主叫",
    value: "7",
  },
];
const bindStatusMap = {
  0: "待绑定",
  1: "绑定中",
  2: "已绑定",
  3: "绑定失败",
  4: "解绑中",
};
const initData = reactive({
  isBind: false,
});
const shareStatusTypes = [
  {
    label: "使用中",
    value: "1",
  },
  {
    label: "待分配",
    value: "0",
  },
];
const { routeQuery } = useVueRouter();
const activeIndex = ref("2");
const changeNumberRef = ref();
const handleNumberRef = ref();
const params = reactive({
  type: routeQuery.value.type,
});
const paramsData = shallowReactive({
  companyId: routeQuery.value.companyId,
  phone: "",
  phoneStatus: "",
  phoneType: activeIndex.value,
});
//企业type=1才执行绑定、解绑操作
const showOperate = computed(() => {
  return (
    activeIndex.value !== "7" || (params.type == 1 && activeIndex.value == "7")
  );
});
const { getList, pageNumberChange, pageSizeChange, pageData, resetPageNumber } =
  usePage({
    listApi: phoneListApi,
    props: {
      pageSize: "pageSize",
      pageNumber: "pageNo",
    },
    searchParams: () => {
      return {
        ...paramsData,
      };
    },
  });
const tabClick = (value) => {
  activeIndex.value = value.value;
  paramsData.phoneType = value.value;
  resetPageNumber();
  getList();
};

const changeNum = (row) => {
  changeNumberRef.value.toggle(true, {
    ...row,
    companyCode: routeQuery.value.companyCode,
    companyId: routeQuery.value.companyId,
    type: params.type,
  });
};
//取消
const handleCancel = ({ callback }) => {
  setTimeout(() => {
    initData.isBind = false;
  }, 300);
  callback && callback();
};
const handleAssign = (row: any) => {
  if (row.bindStatus !== 0 && row.bindStatus !== 2 && row.bindStatus !== 3)
    return;
  if (row.bindStatus == 0 || row.bindStatus == 3) {
    initData.isBind = true;
  } else if (row.bindStatus == 2) {
    initData.isBind = false;
  }
  handleNumberRef.value.handleOpen();
  const { setData } = handleNumberRef.value;
  setData(row);
};
const search = () => {
  resetPageNumber();
  getList();
};
</script>
<style scoped lang="scss">
.search-box {
  @include flex(center, center);

  .el-input {
    margin-right: 10px;
  }

  :deep(.el-range-editor.el-input__wrapper) {
    width: 260px;
    margin-right: 10px;
  }
  .label,
  .text {
    font-size: 14px;
    color: #606266;
  }
  .form {
    display: flex;

    .el-form-item {
      margin-bottom: unset;
    }
  }
}
</style>
